.card {
    border: none;
    position: relative;
    overflow: hidden;
    border-radius: 8px;
    cursor: pointer
}

.card:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 4px;
    height: 100%;
    background-color: #E1BEE7;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    transition: all 0.5s;
    -webkit-transform-origin: bottom;
            transform-origin: bottom
}

.card:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 4px;
    height: 100%;
    background-color: #8E24AA;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: all 0.5s;
    -webkit-transform-origin: bottom;
            transform-origin: bottom
}

.card:hover::after {
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
}

