
.register_logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.register{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
/* //margin-top: ; */
    height:100vh;
    background-color: white;
}
.register_container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border:1px solid lightgray;
    padding: 20px;
}
.register_container>h1{
    font-weight: 500;
    margin-left: 100px;
    margin-bottom: 20px;
}
.register_container> form> h5{
    margin-bottom: 5px;
}
.register_container> form> input{
    height: 30px;
    margin-bottom: 10px;
    font-size: 18px;
    background-color: white;
    width: 98%;
}
.register_container > p{
    margin-top: 15px;
    font-size: 12px;
}
.registerbutton{
    /* background: lightblue; */
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    /* border-color: #a88734 #9c7e31 #846a29; */
}
.loginbutton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}